*{
  margin:0;
  padding:0;
}

body {
  background: #f8f9fa;;
  background-size: cover;
}

main {
  position: relative;
  top: 40px;
}

.download{
  border: 1px solid white;
  border-radius: 12px;
  margin-left: 600px;
}

.row {
  justify-content: space-around;
}

h1, h2, h3 {
  font-weight: 600;
  color: #444;
}

h2{
  font-size: 1.2rem;
}

h3{
  font-family: 'PT Sans Narrow', sans-serif;
}

/* Grey */
#self {
  width: 180px;
  border-radius: 50%;
  filter: drop-shadow(1px 1px 1px black);
  display:block;
  margin:0 auto;
}

.me h1 {
font-family: 'Rock Salt', cursive;
font-size: 2em;
color: #222;
}

.grey {
  background-color: #ddd;
  padding: 30px;
  margin: 20px;
  border-radius: 20px;
  margin-top: 30px;
  opacity: 0.96;
}

.contact {
  width: 30px;
}

/*contact*/
#contact {
  text-decoration: underline;
}

ul {
  list-style: square;
  list-style-position: inside;
  padding-left: 0;

}

a:hover {
  color: #222;
}

a {
  color: #444;
}

.projects{
  font-weight: bold;
}

.projects li{
  cursor: pointer;
}

i.fa{
  font-size: 1.5rem;
  }

  .education{
    list-style: none;
    padding-left: 0;
  }

 .language-level, .year{
   display: block;
   font-size: 0.8rem;
   padding-left: 40px;

 }

 .course{
   margin-bottom: 0;
   padding-left: 20px;
   font-weight: bold;
 }

@media all and (max-width:600px){
  .grey {
    background-color: #ddd;
    padding: 20px;
    border-radius: 15px;
    opacity: 0.96;
  }

}
